@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{
  --negro: #121212;
  --morado: #6809E1;
  --gris: #E7E7E7;
  --backgroundBody: #373737;
  --backgroundList: #212121;
}
.App {
  text-align: center;
  border-top: 4px solid #6809E1;
  border-top: 4px solid var(--morado);
  transition: all 0.5s ease-in-out;
}
.App.dark{
  background-color: #373737;
  transition: all 0.5s ease-in-out;
}
:root{
    --morado: #6809E1;
    --blanco: #ffffff;
}
.container__header{
    width: 900px;
    margin: 0 auto;
    margin-top: 26px;
}
.contenido__header{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.icon__header{
    width: 60px;
    height: 60px;
    padding-left: 79px;
}
.icon__header img{
    width: 60px;
    height: 60px;
}
.button__header{
    padding-right: 68px;
}
.button__header button{
    width: 148px;
    height: 43px;
    border: 1px solid #6809E1;
    border: 1px solid var(--morado);
    border-radius: 61px;
    background-color: #ffffff;
    background-color: var(--blanco);
    cursor: pointer;
    color: #6809E1;
    color: var(--morado);
    font-weight: bold;
    transition: all 0.5s ease-in-out;
}
.button__header.dark button{
    background-color: var(--backgroundBody);
    border: 1px solid #ffffff;
    border: 1px solid var(--blanco);
    color: #ffffff;
    color: var(--blanco);
    transition: all 0.5s ease-in-out;
}
.contenido__imagen{
    margin-top: 41px;
}
.contenido__imagen:nth-child(13),
.contenido__imagen:nth-child(14),
.contenido__imagen:nth-child(15){
    margin-bottom: 41px;
}
.contenido__imagen img{
    width: 250px;
    height: 250px;
}
.contenedor__autocomplete{
    position: absolute;
    left: 0;
    top: 0;
    border: 1px solid var(--morado);
    width: 542px;
    margin-top: 5px;
    border-radius: 20px;
    background-color: white;
}
.contenido__autocomplete{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    text-align: center;
    border-bottom: 1px solid var(--morado);
    padding: 0 20px;
    cursor: pointer;
    text-align: start;
}
.contenido__autocomplete:last-child{
    border-bottom: none;
}
.imagen__lupa{
    margin-right: 0px;
}
.contenido__autocomplete ul{
    padding: 0 10px;
    width: 542px;
}
.contenido__autocomplete ul li{
    font-size: 18px;
    color: #3F3F3F;
    list-style: none;
}
.contenido__autocomplete:hover{
    background-color: thistle;
}
.contenido__autocomplete:hover:first-child{
    border-radius: 20px 20px 0 0;
}
.contenido__autocomplete:hover:last-child{
    border-radius: 0 0 20px 20px;
}
.container__mensaje{
    padding: 23px;
}
.container__mensaje.dark h1,
.container__mensaje.dark p{
    color: var(--blanco);
}
.container__mensaje img{
    width: 100px;
}
.container__search{
    margin-top: 65.89px;
}
.contenido__search{
    font-family: 'Roboto';
    color: var(--morado);
    transition: all 0.5s ease-in-out;
}
.contenido__search.dark h1,
.container__search.dark h2{
    color: white;
    transition: all 0.5s ease-in-out;
}
.imagen__search{
    margin-top: 14px;
}
.container__cardresults{
    width: 900px;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
}
.container__cardresults.light{
    background-color: var(--gris);
    transition: all 0.5s ease-in-out;
    margin-bottom: 40px;
}
.container__cardresults.dark{
    background-color: var(--backgroundList);
    transition: all 0.5s ease-in-out;
}
.container__search h2{
    color: var(--morado);
    margin: 79px 0 31px 0;
    transition: all 0.5s ease-in-out;
}
.lista__autocomplete{
    position: relative;
    left: 0;
    top: 0;
}
.lista__autocomplete:hover{
    background-color: thistle;
}
.formulario__busqueda{
    width: 542px;
    height: 52px;
    margin: 0 auto;
}
.contenido__formulario{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: -4px;
}
.contenido__formulario input[type="text"]{
    width: 450px;
    height: 51px;
    border-radius: 61px 0 0 61px;
    border: 1px solid #121212;
    padding: 0 0 0 20px;
    outline: none;
    font-size: 18px;
    transition: all 0.5s ease-in-out;
}
.contenido__formulario.dark input[type="text"]{
    background-color: var(--backgroundBody);
    color: white;
    border: 1px solid white;
    transition: all 0.5s ease-in-out;
}
.contenido__formulario button{
    width: 86px;
    height: 52px;
    border-radius: 0 61px 61px 0;
    border: 1px solid #121212;
    border-left: none;
    background-color: var(--morado);
    padding: 10px;
    cursor: pointer;
    transition: all 0.5s ease-in-out;
}
.contenido__formulario.dark button{
    border: 1px solid white;
    border-left: none;
    transition: all 0.5s ease-in-out;
}
